@font-face {
  font-family: Gilroy;
  src: url("./../fonts/Gilroy-Light.woff2");
}
@font-face {
  font-family: GilroyBold;
  src: url("./../fonts/Gilroy-ExtraBold.otf");
}
:root {
  --main-color: rgb(0, 0, 0);
  --light-color: rgba(219, 162, 252, 0.532);
  --input-color: rgba(236, 236, 236, 0.2);
}

body {
  margin: 0;
}

.App {
  font-family: Gilroy, sans-serif !important;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  position: relative;
  font-size: 1.2em;
}
@media screen and (max-width: 430px) {
  .App {
    font-size: 1em;
  }
}
.App input,
.App select,
.App button {
  font-family: Gilroy, sans-serif !important;
}
.App main {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  min-height: 55vh;
}
@media screen and (max-width: 1050px) {
  .App main {
    min-height: calc(100vh - (38vh + 50px));
    margin-top: 12vh;
  }
}
.App main .wrapper {
  width: 70vw;
  gap: 20px;
}
@media screen and (max-width: 1050px) {
  .App main .wrapper {
    width: 90vw;
  }
}
.App a {
  color: white;
  text-decoration: none;
}
.App a.active {
  color: rgba(255, 255, 255, 0.747);
}

.wrapperAdmin form button {
  border: 1px solid var(--main-color);
  outline: none;
  background-color: var(--light-color);
  border-radius: 5px;
  color: white;
  font-size: 1em;
}

.wrapperAdmin form input, .wrapperAdmin form select {
  border: 1px solid var(--main-color);
  outline: none;
  background-color: white;
  border-radius: 5px;
  padding: 0 10px;
  color: var(--main-color);
  font-size: 1em;
}
.wrapperAdmin form input::placeholder, .wrapperAdmin form select::placeholder {
  color: var(--light-color);
  font-family: Gilroy;
  font-size: 1em;
}
.wrapperAdmin form input:read-only, .wrapperAdmin form select:read-only {
  background-color: var(--input-color);
}

.wrapperAdmin form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 10px;
}
.wrapperAdmin form select {
  height: 50px;
  width: 320px;
}
.wrapperAdmin form input {
  height: 50px;
  width: 300px;
}
.wrapperAdmin form input::placeholder {
  color: var(--light-color);
  font-family: Gilroy;
  font-size: 1em;
}
.wrapperAdmin form button {
  width: 320px;
  height: 50px;
}
.wrapperAdmin .students {
  margin-top: 50px;
  display: flex;
  gap: 100px;
}
@media screen and (min-width: 700px) {
  .wrapperAdmin .students {
    flex-direction: column;
  }
}
.wrapperAdmin .students > div {
  flex-basis: fit-content;
}
.wrapperAdmin .students .groupName {
  font-size: 1.2em;
  border-bottom: black dotted 2px;
  margin-bottom: 10px;
}

