@font-face {
  font-family: Gilroy;
  src: url("./../fonts/Gilroy-Light.woff2");
}
@font-face {
  font-family: GilroyBold;
  src: url("./../fonts/Gilroy-ExtraBold.otf");
}
:root {
  --main-color: rgb(0, 0, 0);
  --light-color: rgba(219, 162, 252, 0.532);
  --input-color: rgba(236, 236, 236, 0.2);
}

body {
  margin: 0;
}

.App {
  font-family: Gilroy, sans-serif !important;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  position: relative;
  font-size: 1.2em;
}
@media screen and (max-width: 430px) {
  .App {
    font-size: 1em;
  }
}
.App input,
.App select,
.App button {
  font-family: Gilroy, sans-serif !important;
}
.App main {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  min-height: 55vh;
}
@media screen and (max-width: 1050px) {
  .App main {
    min-height: calc(100vh - (38vh + 50px));
    margin-top: 12vh;
  }
}
.App main .wrapper {
  width: 70vw;
  gap: 20px;
}
@media screen and (max-width: 1050px) {
  .App main .wrapper {
    width: 90vw;
  }
}
.App a {
  color: white;
  text-decoration: none;
}
.App a.active {
  color: rgba(255, 255, 255, 0.747);
}

.wrapperTeam {
  display: flex;
  flex-wrap: wrap;
  width: 820px;
  justify-content: center;
}
@media screen and (max-width: 1050px) {
  .wrapperTeam {
    gap: 5px;
  }
}
.wrapperTeam a.card {
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: 400px;
  height: 285px;
  overflow: hidden;
}
@media screen and (max-width: 1050px) {
  .wrapperTeam a.card {
    gap: 5px;
  }
}
.wrapperTeam a.card:hover.image img {
  transform: scale(1.2);
}
.wrapperTeam a.card .image {
  height: 70%;
  overflow: hidden;
}
.wrapperTeam a.card .image img {
  width: 100%;
  transition: transform 0.7s ease-out;
}
.wrapperTeam a.card .name {
  color: black;
  text-transform: uppercase;
  font-weight: bold;
}
.wrapperTeam a.card .desc {
  color: black;
  font-weight: bold;
  font-size: 0.8em;
}
@media screen and (max-width: 1050px) {
  .wrapperTeam a.card .desc {
    font-size: 1em;
  }
}

