.wrapperAbout {
  display: flex;
  flex-wrap: wrap;
  text-align: justify;

  .about-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    width: 100%;

    .img-container {
      max-width: 100%;
      max-height: 50vh;
      display: flex;
      justify-content: center;
      @media screen and (max-width: 1050px) {
        max-height: 30vh;
      }
      @media screen and (max-width: 430px) {
        flex-direction: column;
        max-height: 100%;
        align-items: center;
      }

      img {
        height: 100%;
        @media screen and (max-width: 1050px) {
          max-width: 50%;
        }

        @media screen and (max-width: 430px) {
          height: auto;
        }
      }
    }
  }
}
